<template>
  <div class="home">
    <h1>Welcome</h1>
    <div v-if="!user">
      <LoginForm @login="enterAccount" />
    </div>
    <div v-else>
      <Navbar />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import LoginForm from "../components/LoginForm.vue";
import { useRouter } from "vue-router";
import Navbar from "../components/Navbar.vue";
import getUser from "../composables/getUser";

export default {
  name: "Home",
  components: { LoginForm, Navbar },
  setup() {
    const router = useRouter();
    const { user } = getUser();
    const enterAccount = () => {
      router.push({ name: "Account" });
    };
    return { enterAccount, user };
  },
};
</script>

<style>
a {
  font-weight: bold;
  color: #42b983;
  text-decoration: none;
}

</style>