<template>
  <h1>Account</h1>
  <div class="container" v-if="assets.length > 0">
    <ul>
      <li v-for="a in assets" :key="a.asset_code">
        {{ a.asset_code }}
        {{ a.alias }}
        {{ parseFloat(a.amount).toFixed(a.precision) }}
      </li>
    </ul>
  </div>
  <div v-else>
    <p>
      No assets to show. Please visit our
      <span><router-link to="/store">Store</router-link></span> to buy some
      assets
    </p>
  </div>
</template>

<script>
import getAccount from "../composables/getAccount";
import { ref } from "vue";

export default {
  name: "Account",
  components: {},
  setup() {
    const { accounts, fetchAccount } = getAccount();
    const assets = ref(accounts.value.filter((account) => !account.nft));
    (async () => {
      await fetchAccount();
      assets.value = accounts.value.filter((account) => !account.nft)
    })();

    return { accounts, fetchAccount, assets };
  },
};
</script>

