import { ref } from 'vue'
import getData from "../composables/getData";
import getUser from "../composables/getUser";
import { Decrypt } from "./CipherHandler";
import { getAccountLink } from '../constants/constants';
import { makeHash } from './CipherHandler';
import { assetDefinition, aka } from '../constants/secrets';

const accounts = ref([])
const { user } = getUser();

const fetchAccount = async () => {
    const pair = (await Decrypt(user.value.displayName, user.value.photoURL.slice(8, -4))).slice(1, -1)
    const url = getAccountLink(pair)

    try {
        let response = await fetch(url);
        if (response) {
            let responseJson = await response.json();
            if (responseJson.balances) {
                const balances = responseJson.balances
                let records = await Promise.all(balances.map(async (balance) => {
                    let asset_code = 'GAS'
                    let asset_issuer = 'native'
                    let amount = balance.balance
                    let nft = true
                    let precision = 7
                    let alias = ''
                    if (balance.asset_type != 'native') {
                        asset_code = balance.asset_code
                        asset_issuer = await makeHash.hash(balance.asset_issuer)
                        if (assetDefinition[asset_code]) {
                            if (assetDefinition[asset_code].asset_issuer == asset_issuer) {
                                nft = false
                                precision = assetDefinition[asset_code].precision
                                alias = aka[asset_code]
                            }
                        }
                    } else {
                        nft = false
                        precision = 5
                        alias = aka[asset_code]
                    }
                    let item = {
                        asset_code,
                        amount,
                        asset_issuer,
                        nft,
                        alias,
                        precision
                    }
                    return item
                }))

                accounts.value = records
            }
        }


    } catch (err) {
        console.log('cannot find account');
    };
}

const getAccount = () => {
    return { accounts, fetchAccount }
}


export default getAccount