<template>
  <nav>
    <div v-if="user">
      <p>ID: {{ user.uid }}</p>
      <p>Currently logged in as {{ user.email }}</p>
      <button @click="handleClick">Logout</button>
    </div>
    <div v-else>
      <button @click="goHome">Back to Login</button>
    </div>
  </nav>
</template>

<script>
import useLogout from "../composables/useLogout";
import getUser from "../composables/getUser";
import getData from "../composables/getData";
import { useRouter } from "vue-router";



export default {
  components: {  },
  setup() {
    const { logout, error } = useLogout();
    const { user } = getUser();
    const { api, profile } = getData();

    const router = useRouter();

    const handleClick = async () => {
      await logout();
      if (!error.value) {
        console.log("user logged out");
      }
    };
    const goHome = () => {
      router.push({ name: "Home" });
    };

    const getProfile = async () => {
      if (!api.value) {
        // console.log("uid", user.value.uid);
        // console.log("email", user.value.email);
        // console.log("photo url", user.value.photoURL);
        // console.log("display name", user.value.displayName);
        const userProfile = await profile();
        // console.log(userProfile);
      }
    };
    getProfile();
    return { handleClick, user, goHome, api };
  },
};
</script>

