import { ref } from 'vue'
import { projectFirestore } from '../firebase/config'
import { projectAuth } from '../firebase/config'
import { Encrypt, Decrypt } from "./CipherHandler";
var StellarSdk = require('stellar-sdk');

const api = ref(null)

const profile = async () => {
    let user = projectAuth.currentUser
    // console.log(user)
    if (user.photoURL && user.displayName) {
        let pair = (await Decrypt(user.displayName, user.photoURL.slice(8, -4))).slice(1, -1)
        var pass = false
        try {
            StellarSdk.Keypair.fromPublicKey(pair)
            pass = true
        } catch (err) {
            pass = false
        }
        if (pass) {
            api.value = user.photoURL.slice(8, -4)
            return
        }
    }
    var docRef = projectFirestore.collection("federate").doc(user.uid)
    const doc = await docRef.get()
    if (doc.exists) {

        api.value = doc.data().userHash
        const xid = (await Encrypt(doc.data().xid, doc.data().userHash)).toString()
        // console.log('profile', doc.data())
        await user.updateProfile({
            displayName: xid,
            photoURL: 'https://' + doc.data().userHash + '.png'
        })
        return doc.data()
    }
}

const getData = () => {
    return { api, profile }
}

export default getData