import * as sjcl from './sjcl';
const Hash = require('ipfs-only-hash');
const bs58 = require('bs58')

export const getEnDict = () => {
    return ["iv", "salt", "ct"];
};

export const makeHash = {
    hash: async (data) => {
        const q = (await Hash.of(data))
        return q
    },
    hq: (h) => {
        const b = Buffer.from('1220' + h, 'hex')
        const q = bs58.encode(b)
        return q
    },
    qh: (q) => {
        const m = bs58.decode(q)
        const h = m.toString('hex').slice(4,)
        return h
    }
};

export const getBaseDict = () => {
    return { "v": 1, "iter": 10000, "ks": 128, "ts": 64, "mode": "ccm", "adata": "", "cipher": "aes" };
};

export const Base64 = { _keyStr: "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=", encode: function (e) { var t = ""; var n, r, i, s, o, u, a; var f = 0; e = Base64._utf8_encode(e); while (f < e.length) { n = e.charCodeAt(f++); r = e.charCodeAt(f++); i = e.charCodeAt(f++); s = n >> 2; o = (n & 3) << 4 | r >> 4; u = (r & 15) << 2 | i >> 6; a = i & 63; if (isNaN(r)) { u = a = 64 } else if (isNaN(i)) { a = 64 } t = t + this._keyStr.charAt(s) + this._keyStr.charAt(o) + this._keyStr.charAt(u) + this._keyStr.charAt(a) } return t }, decode: function (e) { var t = ""; var n, r, i; var s, o, u, a; var f = 0; e = e.replace(/[^A-Za-z0-9\+\/\=]/g, ""); while (f < e.length) { s = this._keyStr.indexOf(e.charAt(f++)); o = this._keyStr.indexOf(e.charAt(f++)); u = this._keyStr.indexOf(e.charAt(f++)); a = this._keyStr.indexOf(e.charAt(f++)); n = s << 2 | o >> 4; r = (o & 15) << 4 | u >> 2; i = (u & 3) << 6 | a; t = t + String.fromCharCode(n); if (u != 64) { t = t + String.fromCharCode(r) } if (a != 64) { t = t + String.fromCharCode(i) } } t = Base64._utf8_decode(t); return t }, _utf8_encode: function (e) { e = e.replace(/\r\n/g, "\n"); var t = ""; for (var n = 0; n < e.length; n++) { var r = e.charCodeAt(n); if (r < 128) { t += String.fromCharCode(r) } else if (r > 127 && r < 2048) { t += String.fromCharCode(r >> 6 | 192); t += String.fromCharCode(r & 63 | 128) } else { t += String.fromCharCode(r >> 12 | 224); t += String.fromCharCode(r >> 6 & 63 | 128); t += String.fromCharCode(r & 63 | 128) } } return t }, _utf8_decode: function (e) { var t = ""; var n = 0; var c1, c2; var r = c1 = c2 = 0; while (n < e.length) { r = e.charCodeAt(n); if (r < 128) { t += String.fromCharCode(r); n++ } else if (r > 191 && r < 224) { c2 = e.charCodeAt(n + 1); t += String.fromCharCode((r & 31) << 6 | c2 & 63); n += 2 } else { c2 = e.charCodeAt(n + 1); c3 = e.charCodeAt(n + 2); t += String.fromCharCode((r & 15) << 12 | (c2 & 63) << 6 | c3 & 63); n += 3 } } return t } }

export const Encrypt = async (message, key = '', refreshRate = 0) => {
    const enDict = getEnDict();
    var encrypted = {};
    try {
        if (refreshRate > 0) {
            key = key.toString() + Math.floor(Date.now() / (1000 * refreshRate)).toString();
        };
        if (typeof message == 'object') {
            message = JSON.stringify(message);
        }
        const encryptedObj = sjcl.encrypt(key, message);
        let encryptedJSON = await JSON.parse(encryptedObj);
        enDict.forEach(i => {
            encrypted[i] = encryptedJSON[i];
        });
        return Base64.encode(JSON.stringify(encrypted));
    } catch (err) {
        return JSON.stringify(encrypted);
    };
};

export const Decrypt = async (encrypted, key = '', refreshRate = 0) => {
    var message = "";
    try {
        if (refreshRate > 0) {
            key = key.toString() + Math.floor(Date.now() / (1000 * refreshRate)).toString();
        };
        message = sjcl.decrypt(key, Base64.decode(encrypted.toString()));
        return message;
    } catch (err) {
        return message;
    };
};

export const edCoder = {
    baseTxt: "0123456789abcdefghijklmnopqrstuvwxyzABCEDFGHIJKLMNOPQRSTUVWXYZ",
    encode: (numText) => {
        const l = edCoder.baseTxt.length;
        var code = ""
        var num = Math.trunc(parseFloat(numText));
        if (num.toString() != 'NaN') {
            if (Math.floor(Number.MAX_SAFE_INTEGER) > num) {
                while (num > 0) {
                    code = code + edCoder.baseTxt[num % l]
                    num = Math.trunc(num / l)
                }
            }
        }
        return code
    },
    decode: (encodedTxt) => {
        const l = edCoder.baseTxt.length;
        var num = 0;
        var s = 0;
        var pass = true;
        for (var i = 0; i < encodedTxt.length; i++) {
            s = edCoder.baseTxt.indexOf(encodedTxt[i]);
            if (s == -1) {
                pass = false;
            }
            num = num + s * l ** i
        }
        if (Math.floor(Number.MAX_SAFE_INTEGER) <= num || !pass) {
            num = 0
        }
        return num.toString()
    }
};