<template>
  <h1>Trade</h1>
  <div id="infoList">
    <div @click="toggleOpen(trade.HKAO)">
      {{ trade.HKAO.desc }}
    </div>
    <div v-show="compareOpen(trade.HKAO)">
      <QRcode :text="trade.HKAO.get[0].qrcode" />
    </div>

    <div @click="toggleOpen(trade.GAS)">
      {{ trade.GAS.desc }}
    </div>
    <div v-show="compareOpen(trade.GAS)">
      <QRcode :text="trade.GAS.get[0].qrcode" />
    </div>

    <div @click="toggleOpen(trade.nft)">
      {{ trade.nft.desc }}
    </div>
    <div v-show="compareOpen(trade.nft)">
      <QRcode :text="trade.nft.get[0].qrcode" />
    </div>

    <div @click="toggleOpen(trade.auction)">
      {{ trade.auction.desc }}
    </div>
    <div v-show="compareOpen(trade.auction)">
      <QRcode :text="trade.auction.get[0].qrcode" />
    </div>
  </div>
</template>

<script>
import { trade } from "../constants/trade";
import QRcode from "../components/QRcode.vue";
import { ref } from "vue";

export default {
  components: { QRcode },
  setup() {
    const isOpen = ref("");
    const qrContent = ref(" ");

    const compareOpen = ref((index) => {
      const result = JSON.stringify(isOpen.value) == JSON.stringify(index);
      return result;
    });

    const toggleOpen = (index) => {
      const selected = JSON.stringify(index);
      const isOpenTxt = JSON.stringify(isOpen.value);
      if (selected == isOpenTxt) {
        isOpen.value = "";
      } else {
        isOpen.value = index;
      }
    };
    return { trade, isOpen, toggleOpen, compareOpen, qrContent };
  },
};
</script>

